<template>
  <div>
    <el-page-header @back="goBack" content="课程学习情况"> </el-page-header>
    <el-descriptions title="课程信息">
      <el-descriptions-item label="课程ID">{{
        this.course.courseID
      }}</el-descriptions-item>
      <el-descriptions-item label="课程名字">{{
        this.course.courseName
      }}</el-descriptions-item>
      <el-descriptions-item label="课程描述">{{
        this.course.description
      }}</el-descriptions-item>
    </el-descriptions>
    <br />

    <p>选课学生表</p>
    <el-table :data="students" style="width: 100%">
      <el-table-column prop="studentID" label="学生ID" width="180">
      </el-table-column>
      <el-table-column prop="name" label="姓名" width="180"> </el-table-column>
      <el-table-column prop="grade" label="年级" width="180"> </el-table-column>
      <el-table-column prop="className" label="班级"> </el-table-column>
    </el-table>
    <el-pagination
      :current-page="currentPage"
      :page-sizes="[5, 10, 15]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
      @size-change="handleSizeChange"
      @current-change="handlePageChange"
    >
    </el-pagination>

    <h1>讨论区</h1>
    <div class="discussion">
      <div v-for="(reply, index) in replies" :key="index" class="message">
        <div class="user-info">
          <span v-if="isTeacher(reply.replierID)" class="teacherName">{{
            reply.replierName
          }}</span>
          <span v-if="isStudent(reply.replierID)" class="studentName">{{
            reply.replierName
          }}</span>
          <span class="time">{{
            formatReplyDateTime(reply.replyDateTime)
          }}</span>
          <span class="deleteReply"
            ><el-button
              type="primary"
              icon="el-icon-delete"
              @click="deleteReply(reply)"
            ></el-button
          ></span>
        </div>
        <div class="content">
          {{ reply.content }}
        </div>
      </div>
    </div>
    <el-pagination
      :current-page="currentPage1"
      :page-sizes="[5, 10, 15]"
      :page-size="pageSize1"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
      @size-change="handleSizeChange1"
      @current-change="handlePageChange1"
    >
    </el-pagination>
    <el-form :inline="true" :model="newReply">
      <el-form-item label="评论">
        <el-input
          type="textarea"
          v-model="newReply.content"
          placeholder="输入评论"
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitNewReply">发送评论</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
  
  <script>
import axios from "axios";
export default {
  data() {
    let course = this.$route.query;
    let courseID = course.courseID;
    return {
      course: course,
      courseID: courseID,

      chapters: [],
      students: [],
      currentPage: 1,
      pageSize: 5,
      total: 100,

      replies: [],
      currentPage1: 1,
      pageSize1: 5,
      total1: 100,

      newReply: {
        replierID: localStorage.getItem("id"),
        replierName: localStorage.getItem("name"),
        content: "",
        replyDateTime: "",
        courseID: courseID,
      },
    };
  },
  methods: {
    deleteReply(reply) {
      this.$confirm("确认删除此评论?")
        .then(() => {
          axios
            .get(
              "http://localhost:8080/SongGuo_Learning/discussion/deleteReply?replierID=" +
                reply.replierID +
                "&replyDateTime=" +
                reply.replyDateTime +
                "&courseID=" +
                reply.courseID,
              {
                headers: {
                  Token: localStorage.getItem("token"),
                },
              }
            )
            .then((resp) => {
              console.log(resp.data);
              if (resp.data.message == "DELETE_OK") {
                this.$notify.success({
                  title: "操作成功",
                  message: "您已成功删除一条评论!",
                });
              }
            });
        })
        .catch(() => {});
    },
    goBack() {
      this.$router.push("/teacher_homepage_personal_info");
    },
    handleSizeChange(val) {
      this.pageSize = val;
      this.getStudentData();
    },
    handlePageChange(val) {
      this.currentPage = val;
      this.getStudentData();
    },
    handleSizeChange1(val) {
      this.pageSize1 = val;
      this.getReplyData();
    },
    handlePageChange1(val) {
      this.currentPage1 = val;
      this.getReplyData();
    },
    formatReplyDateTime(timestamp) {
      const date = new Date(timestamp);
      return date.toLocaleString();
    },
    isTeacher(replierID) {
      return replierID.toString().charAt(0) === "1";
    },
    isStudent(replierID) {
      return replierID.toString().charAt(0) === "2";
    },
    submitNewReply() {
      axios
        .post(
          "http://localhost:8080/SongGuo_Learning/discussion/addReplyToCourse",
          this.newReply,
          {
            headers: {
              Token: localStorage.getItem("token"),
            },
          }
        )
        .then((resp) => {
          console.log(resp.data);
          this.$notify.success({
            title: "发送成功",
            message: "您已成功发送一条评论",
          });
        });
    },
    getStudentData() {
      axios
        .get(
          "http://localhost:8080/SongGuo_Learning/teacher/getEnrolledStudents?courseID=" +
            this.courseID +
            "&pageSize=" +
            this.pageSize +
            "&currentPage=" +
            this.currentPage,
          {
            headers: {
              Token: localStorage.getItem("token"),
            },
          }
        )
        .then((resp) => {
          console.log(resp.data);
          this.students = resp.data.data;
        });
    },
    getReplyData() {
      axios
        .get(
          "http://localhost:8080/SongGuo_Learning/discussion/getCourseReplies?courseID=" +
            this.courseID +
            "&pageSize=" +
            this.pageSize1 +
            "&currentPage=" +
            this.currentPage1,
          {
            headers: {
              Token: localStorage.getItem("token"),
            },
          }
        )
        .then((resp) => {
          console.log(resp.data);
          if (resp.data.message == "TOKEN_IS_NULL") {
            this.$router.push("/studentlogin");
            return;
          }

          this.replies = resp.data.data;
        });
    },
  },
  mounted() {
    this.getStudentData();
    this.getReplyData();
  },
};
</script>
  
  <style scoped>
.containerOfComponents {
  display: flex;
  flex-direction: column;
}

.discussion {
  margin-top: 20px;
}

.message {
  margin-bottom: 10px;
  border: 1px solid #ccc;
  padding: 10px;
  border-radius: 5px;
}

.user-info {
  margin-bottom: 5px;
  font-weight: bold;
}

.content {
  color: #333;
}

.teacherName {
  margin-right: 10px;
  color: aqua;
}

.studentName {
  margin-right: 10px;
  color: green;
}

.time {
  margin-right: 10px;
}
</style>